.cta-block,
.editor-block-list__layout .cta-block {
  @include spacer_bottom;
  background: $color-primary;
  overflow: hidden;
  width: 100%;

  position: relative;
}

.cta-block__inner,
.editor-block-list__layout .cta-block__inner {
  position: relative;
  z-index: 2;
}

.cta-block__body,
.editor-block-list__layout .cta-block__body {
  width: 100%;

  @include break-min($sm) {
    min-height: 400px;
  }
}

.cta-block__content,
.editor-block-list__layout .cta-block__content {
  background-color: #E3EAEE;
  border-top: 8px solid lighten($color-primary, 25%);
  color: $color-secondary;
  margin-left: auto;
  padding: $item-padding;

  @include break-min($sm) {
    margin-bottom: $spacer-medium;
    padding: $spacer-medium;
    width: 50%;
  }

  @include break-min($md) {
    padding: $spacer-xlarge $spacer-medium $spacer-xlarge $spacer-large;
  }

  & *:last-child {
    margin-bottom: 0;
  }
}

h3.cta-block__title,
.editor-block-list__layout h3.cta-block__title {
  color: currentColor;
  margin-bottom: $spacer-small;
}

p.cta-block__text,
.editor-block-list__layout .cta-block__text {
  font-size: 18px;
  margin-bottom: $spacer-small;
}

a.cta-block__link,
.editor-block-list__layout a.cta-block__link {
  @include button_primary;
  margin: 0 !important;

  &.cta-block__link--external {
    display: inline-flex;
    justify-content: center;

    svg {
      margin-left: 5px;
      width: 14px;
    }
  }
}

.cta-block__background-image,
.editor-block-list__layout .cta-block__background-image {
  display: block;

  @include break-min($sm) {
    @include absolute_fill;
    font-family: 'object-fit: cover;';
    object-fit: cover;

    z-index: 1;
  }
}
